import React, {Component} from 'react';
import TimeOut from '../../components/TimeOut/TimeOut';
import {hashHistory} from 'react-router';
import './Five20.css';
import FiveTwoZeroModule from '../../components/Module/520/FiveTwoZeroModule'
import {get520Data,submitActivityOrder} from '../../services/api';
import defaultModule from '../../components/Module/defaultModule/defaultModule';
import showModule from '../../components/Module/showModule';
import {weChatPay} from '../../components/WechatPay/wechatPay';
class Five20 extends Component {
    constructor(props) {
        super(props);
        this.state={
            goodsList:[],
            isBegin: false,
            hasTimer: true
        }
    }

    componentDidMount() {
        localStorage.setItem('FTO',JSON.stringify(this.props.location.query));
        get520Data(Object.assign({},{...JSON.parse(localStorage.getItem('FTO'))},{...this.props.location.query},{activityId:20180520})).then(data => {
            console.log(!data.data.activityGoods.every((item)=>{return item.state===0}));

            this.setState({
                hasStock: !data.data.activityGoods.every((item)=>{return item.state===0}),
                killCode: data.data.killCode,
                mobile: data.data.mobile,
                goodsList: data.data.activityGoods
            })

        });
    }
    toManagementPage = () => {
        hashHistory.push({
            pathname: '/management',
            query: {
                activityId: 20180520,
                mobile: this.state.mobile,
            }
        })
    }
    toDetailPage = (item) => {
        hashHistory.push({
            pathname: '/goodsDetail',
            query: {...item}
        })
    }
    toHot = ()=>{
        window.location.href = 'http://yxcat.com.cn/ddp-yxcat/index.html'
    }
    getGoods = (e,item) => {
        e.stopPropagation();
        // console.log(this.state.isBegin,this.state.hasStock);
        if (!this.state.isBegin){
            showModule(<FiveTwoZeroModule bodyText={"客官来早了哦~秒杀还未开始，<br>先去其他的活动转转吧~"} btnTitle="爆款专区" btnfun={this.toHot}/>);
            return false;
        }
        if(item.state===0){
            showModule(<FiveTwoZeroModule bodyText={"亲，手慢了哎，宝贝已抢光～<br>摸摸头明天再来呗～"} btnTitle="爆款专区" btnfun={this.toHot}/>);
            return false;
        }
        submitActivityOrder(Object.assign({},{...item},{num:1,mobile: this.state.mobile})).then(data=>{
            if (data.code==="10003"){
                showModule(<FiveTwoZeroModule bodyText={"亲，手慢了哎，宝贝已抢光～<br>摸摸头明天再来呗～"} btnTitle="爆款专区" btnfun={this.toHot}/>);
            } else if(data.code==="0000"){
                showModule(<FiveTwoZeroModule bodyText={`<div>亲，${item.goodsName}</div><div class='redModuleText'>已秒到名额</div>`} btnTitle="立即支付" btnfun={()=>weChatPay({
                    "orderId": data.orderId,
                    "payFee": data.payFee,
                    "body": data.body,
                    "payType": "MWEB",
                    "businessType": 'activity',
                    "attach": JSON.stringify({'attach':'activity'})
                },'yxcat.com.cn/ddp-activities/#management?activityId=20180520&mobile='+this.state.mobile)}/>);
                // hashHistory.push({
                //     pathname: '/management',
                //     query: {
                //         goodsId: item.goodsId
                //     }
                // })
            }else if(data.code==="1002"){
                defaultModule("提示",data.message, false,1)
            }else if(data.code==="1001"){
                showModule(<FiveTwoZeroModule bodyText={"客官来早了哦~秒杀还未开始，<br>先去其他的活动转转吧~"} btnTitle="爆款专区" btnfun={this.toHot}/>);
            }else{
                defaultModule("提示",data.message, false,1)
            }
        });

    }
    getCardDom = () => {
        return (
            <div>
                {
                    this.state.goodsList && this.state.goodsList.map((item,key) => {
                        return (
                            <div key={key} className="card520" onClick={()=>this.toDetailPage(item)}>
                                <img alt="goodsIcon"
                                     src={item.goodsImage}/>
                                <div className="goodsInfo"
                                     >
                                    <div>{item.goodsName}</div>
                                    <div>{item.goodsTitle}</div>
                                    <div className="btnDiv">
                            <span
                                className="red">¥{item.salePrice}</span>
                                        <span
                                            className="textThrough spantext">¥{item.showPrice}</span>
                                        <button className={item.state===0? 'grayBtn' :''} onClick={(e)=>this.getGoods(e,item)}>
                                            立即购买
                                        </button>
                                    </div>
                                </div>
                            </div>
                        );
                    })
                }
            </div>
        )

    }
    isBegin = (_second)=>{
        if(_second<0){
            this.setState({isBegin: true})
        }
        if(_second<0 && this.state.hasStock){
            this.setState({hasTimer: false});
        }
    }
    render() {
        return (
            <div>
                <div className="backgroundOrange tiltle520" onClick={this.toManagementPage}>
                    <div className="tiltle520_first">我的订单
                    </div>
                    <div className="tiltle520_second">
                        查看我的订单
                    </div>
                    <img alt="back>"
                         src="assets/img/back>.png"/>
                </div>
                <img className="banner520" alt=""
                     src="assets/img/520/520banner.jpg"/>
                <img className="timer520" alt="timer"
                     src="assets/img/timerOut.png"/>
                <span className="timeOutLine">距本场开始</span>
                <TimeOut hasTimer={this.state.hasTimer} datumTime={[10, 0, 0, 0]} getTimeValue={this.isBegin}/>
                {this.getCardDom()}
            </div>
        )
    }


}
export default Five20


